export const whatsOn = {
    methods: {
        scrollLeft() {
            const el = this.$refs.scrollable;
            if (el.scrollTo) {
                return el.scrollTo({
                    top: 0,
                    left: el.scrollLeft - el.offsetWidth*2/3,
                    behavior: 'smooth'
                })
            }

            return this.$refs.scrollable.scrollLeft += 320
        },
        scrollRight() {
            const el = this.$refs.scrollable;
            const width = el.offsetWidth;
            
            this.scrolled = true;

            if (el.scrollTo) {
                return el.scrollTo({
                    top: 0,
                    left: el.scrollLeft + el.offsetWidth*2/3,
                    behavior: 'smooth'
                })
            }

            return this.$refs.scrollable.scrollLeft += 320
        }
    },
    template: /*html*/`
        <div class="whats-on__scroll" ref="scrollable">
            <slot></slot>
            <button class="whats-on__scroll-left btn is-red" @click="scrollLeft">&lt;</button>
            <button class="whats-on__scroll-right btn is-red" @click="scrollRight">&gt;</button>
        </div>
    `
}