export const videoViewer = {
    props: {
        id: String,
        parent: String
    },
    data() {
        return {
            open: false
        }
    },
    methods: {
        close() {
            this.open = false;
            this.$refs[this.id].scrollIntoView({behavior: "auto"});
        }
    },
    template: /*html*/`
        <div class="video-viewer" :ref="id">
            <span @click="open = true">
                <slot name="opener"></slot>
            </span>
            <div v-if="open" class="video-viewer__backdrop" id="media" @click="close">
                <div class="video-viewer__video">
                    <slot></slot>
                </div>
                <a class="video-viewer__close" :href="id" @click.prevent="close">
                    &times;
                </a>
            </div>
        </div>
    `
};
